import React, { Component } from 'react';
import {Fab, withStyles} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import FieldList from './FieldList';
import { withRouter } from "react-router";
import classNames from 'classnames';

const styles = theme => ({
  fab: {
    position: 'absolute',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  }
});

let HomeCmpt = class Home extends Component {

  constructor(props){
    super(props);
    this.gotoAddField = this.gotoAddField.bind(this);
    this.state  = { fields: []}
  }

  gotoAddField() {
    this.props.history.push('/addField');
  }

  renderFields () {
    return this.state.fields.map((f) => (
        <div>Name: {f.name} : {f.type}</div>
    ))
  }

  render() {
    const { classes } = this.props;
    return (
        <div>
          <FieldList fields={this.props.fields}/>
          <Fab color="primary" aria-label="Add" className={classNames(classes.fab)}>
            <AddIcon onClick={this.gotoAddField}/>
          </Fab>
        </div>
    );
  };
}

export default withStyles(styles)(withRouter(HomeCmpt));